.movies {
  display: flex;
  justify-content: center;
  margin-bottom: 54%;
}
@media only screen and (max-width: 760px) {
  .movies {
    top: 150px;
    position: relative;
  }
  .list-movies {
    width: 50%;
  }
}

.list-movies {
  // margin-bottom: 100%;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  flex-direction: column;
  justify-content: flex-start;
}
.filter {
  height: 150px;
  display: flex;
  width: 100%;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: flex-end;
  justify-content: flex-start;
  align-content: flex-end;
}
